import React, { useState }  from 'react'
import 'firebase/auth';
import {useFirebaseApp, useUser} from 'reactfire';

import style from './Auth.module.css';


export default (props) => {
    
    const [email, setEmail] = useState('');
    const[password,setPassword] =useState('');
    
    const firebase = useFirebaseApp();
    const user = useUser()
    
    const submit = async () => {
        await firebase.auth().createUserWithEmailAndPassword(email,password);
    }

    const login = async () => {
        await firebase.auth().signInWithEmailAndPassword(email,password);
    }


    const logout = async ()=> {
        await firebase.auth().signOut();
    }

 
    return(
        <div>
            {
                !user && 
                <div>
                    <label htmlFor="email">E-mail </label>
                    <input className={style.input} type="email" id="email" onChange={(ev)=>setEmail(ev.target.value)}/>
                    <br/>
                    <label htmlFor="password">Contraseña </label>
                    <input className={style.input} type="password" id="password" onChange={(ev)=>setPassword(ev.target.value)}/>
                    <p>
                        <button onClick={login} className = {style.btn}>Iniciar sesión</button>
                        <button onClick={submit} className = {style.btn}>Crear cuenta</button>
                    </p>

                </div>
            }
            
            {
                user && <button className = {style.btn} onClick={logout}>Cerrar sesión</button>
            }

        </div>
    )
}

