import React from 'react';
import logo from './logo.svg';
import './App.css';

import {
  useUser
} from 'reactfire';

import Auth from './Auth';

import Suma from './components/Suma/Suma';
import { Resta } from "./components/Resta/Resta";
import Multiplicacion from './components/Multiplicacion/Multiplicacion';
import VerMas from './components/VerMas/VerMas'

function App() {


  const user = useUser();
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />

        {
          user && 
          <div>
            <p>
              Hola!
            </p>
            <p>
              Iniciaste sesión con {user.email}
            </p>
            <hr></hr>
              <Suma/>
            <hr></hr>
              <Resta/>
            <hr/>
              <Multiplicacion/>
            <hr/>

          </div>
        }
        <Auth/>

        <VerMas/>

      </header>
      <div id = "explicacion">
       <p>Esta web fue creada usando Auth de Firebase. Una "calculadora" super simple que para poder ingresar debés crearte una cuenta e iniciar sesión.</p>

       <p>Las cuentas son guardadas en la BDD de Firebase encriptadas. Así que tranquilo, tu contraseña está segura.</p>

       <p>Todo está estilizado con módulos CSS, creando las clases de los elementos de forma optima.</p>

        <p>Para conocer más podés ingresar a el <a href="https://github.com/alepiumetti/calculadora-simple-react">repositorio</a> de esta web</p>


      </div>

      <div>
        
      </div>
    </div>
  );
}

export default App;
