import React, { Component } from 'react';

import style from './Suma.module.css';

export class Suma extends Component {
    
    constructor (props){
        super(props);

        this.state = {
            resultado: '',
            nro1:'' ,
            nro2:'' ,

        }
    }


    syncChanges(value,property){
        let state = {}
        state[property] = value;
        this.setState(state);
      }

    
    validation = () =>{
        

          if ( this.state.nro1==="" || this.state.nro2===""){
                alert("Ingrese un número válido");
            }
            else{
                this.sumar();
            }
    }

    sumar = () => {


        this.setState({
            resultado: parseFloat(this.state.nro1) + parseFloat(this.state.nro2)
        })
    }
    
    render() {
        return (
            <div>   
                <h3>Suma de 2 números</h3>
                <input 
                className = {style.input}
                type = "number" 
                placeholder = "Ingresá un Número" 
                onChange={(ev)=>{ this.syncChanges(ev.target.value,'nro1')}} />
                <span> + </span>
                <input 
                className = {style.input}
                type = "number" 
                placeholder = "Ingresá otro Número"  
                onChange={(ev)=>{ this.syncChanges(ev.target.value,'nro2')}} />
                <span> = </span>
                <input 
                className = {style.result}
                type = "number" 
                readOnly 
                value = {this.state.resultado} />
                <br/>
                <button 
                className = {style.btn}
                onClick = {this.validation}
                >Sumar</button>
            </div>
        )
    }
}


export default Suma
